:root {
  --font-family: "FuturaNew", sans-serif;
  --content-width: 1160px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --accent-color: #ff623d;
  --black-color: #333;
  --brown-color: #f4e9bf;
  --grey-color: #828282;
  --grey-text-color: #4f4f4f;
  --light-color: #e5e5e5;
  --white-color: #fff;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Bold.woff2") format("woff2"), url("../FuturaNew-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Book.woff2") format("woff2"), url("../FuturaNew-Book.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Demi.woff2") format("woff2"), url("../FuturaNew-Demi.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Heavy.woff2") format("woff2"), url("../FuturaNew-Heavy.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Light.woff2") format("woff2"), url("../FuturaNew-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
  object-fit: cover;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

a {
  text-decoration-color: transparent;
  color: inherit;
}

address {
  font-style: normal;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--grey-text-color);
}

.lock {
  overflow: hidden;
  touch-action: none;
}

.page {
  font-family: var(--font-family, sans-serif);
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
}

.logo__img {
  object-fit: contain;
}

.sr-only {
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  position: absolute;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  width: 1px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  overflow: hidden;
  position: absolute;
  height: 1px;
  width: 1px;
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--main-color-dark);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}

.social-list__item:not(:last-child) {
  margin-right: 35px;
}
.social-list__item--line:not(:last-child) {
  padding-right: 35px;
  position: relative;
}
.social-list__item--line:not(:last-child)::after {
  content: "";
  background-color: var(--grey-text-color);
  display: block;
  width: 1px;
  height: 16px;
  position: absolute;
  top: 3px;
  right: 0;
}
.social-list__link {
  display: flex;
  align-items: center;
  color: var(--light-color);
  font-size: 15px;
  font-weight: 400;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .social-list__link:hover {
    color: var(--brown-color);
  }
  .social-list__link:hover .social-list__name::after {
    background-color: var(--brown-color);
  }
}
.social-list__link svg {
  margin-right: 15px;
}
.social-list__name {
  position: relative;
  padding: 0 1px;
}
.social-list__name::after {
  content: "";
  display: block;
  background-color: var(--light-color);
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: -1px;
  left: 0;
  transition: background-color 0.3s ease 0s;
}

.call {
  color: var(--white-color);
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .call:hover {
    color: var(--brown-color);
  }
}
.call svg {
  fill: currentColor;
  width: 17px;
  height: 17px;
  margin-right: 15px;
}

.menu {
  display: flex;
}
.menu__item {
  color: var(--white-color);
  font-size: 16px;
  position: relative;
  padding-right: 15px;
}
.menu__item:not(:last-child) {
  margin-right: 40px;
}
.menu__item--blog {
  padding-right: 23px;
  position: relative;
}
.menu__item--blog:before {
  content: "";
  display: block;
  background-image: url(../images/svg/fire.svg);
  position: absolute;
  width: 15px;
  height: 16px;
  right: 0;
  bottom: 4px;
}
.menu__link {
  transition: color 0.3s ease 0s;
  position: relative;
  padding-bottom: 1px;
}
.menu__link::after {
  content: "";
  display: block;
  background-color: var(--light-color);
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: -1px;
  left: 0;
  transition: background-color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .menu__link:hover {
    color: var(--brown-color);
  }
  .menu__link:hover:after {
    background-color: var(--brown-color);
  }
}
.menu__link svg {
  fill: currentColor;
  width: 7px;
  height: 7px;
  position: absolute;
  right: -15px;
  top: 4px;
  transition: top 0.3s ease 0s;
}
.menu__link--active {
  color: var(--brown-color);
}
.menu__link--active:after {
  background-color: var(--brown-color);
}
.menu__link--active svg {
  top: 15px;
}
.menu__dropdown {
  position: absolute;
  top: 40px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out 0s, visibility 0.3s ease-in-out 0s;
}
.menu__dropdown--active {
  opacity: 1;
  visibility: visible;
}

.dropdown {
  background-color: var(--white-color);
  box-shadow: 0px 4px 20px 1px rgba(0, 0, 0, 0.25);
}
.dropdown--catalog {
  padding: 17px 27px 35px 35px;
  min-width: 285px;
  right: -95px;
}
.dropdown--catalog .dropdown__list:before {
  right: 40%;
}
.dropdown--info {
  padding: 16px 35px 35px 35px;
  min-width: 220px;
  right: -25px;
}
.dropdown--info .dropdown__list:before {
  right: 20%;
}
.dropdown__list {
  position: relative;
}
.dropdown__list:before {
  content: "";
  display: block;
  border: 20px solid transparent;
  border-bottom: 10px solid var(--white-color);
  position: absolute;
  top: -41px;
}
.dropdown__item:not(:last-child) {
  margin-bottom: 15px;
}
.dropdown__link {
  color: var(--black-color);
  font-size: 16px;
  position: relative;
  padding: 0 1px;
  transition: color 0.3s ease 0s;
}
.dropdown__link::after {
  content: "";
  display: block;
  background-color: var(--black-color);
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: -1px;
  left: 0;
  transition: background-color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .dropdown__link:hover::after {
    background-color: var(--accent-color);
  }
}

.header {
  background-color: var(--black-color);
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
}
.header__social {
  display: flex;
}
.header__social svg {
  width: 18px;
  height: 18px;
  fill: var(--grey-text-color);
}
.header__bottom {
  display: flex;
  justify-content: space-between;
  padding: 45px 0 25px 0;
}
.header__nav {
  display: flex;
}
.header__logo {
  flex-shrink: 0;
  margin-right: 60px;
}